import React, { Component } from "react";
import { Switch, Route } from 'react-router-dom';
import MyFaq from "./faq.json";
import Myjson from "./data.json";
import { Snikos3d } from './components/Snikos3d';
import { Header } from './Header';
import { Sidebar } from './Sidebar';
import { Footer } from './Footer';
import { GiveAStart } from './components/GiveAStart';
import { Faq } from './components/Faq';
import { PageNotFound } from './components/PageNotFound';
import "./css/App.css";
import "./css/myquality.css";
import './css/oil.css';
import './css/btncool.css';
import './css/snikos3d.css';

export default class App extends Component {

    constructor(props) {
      super(props);
      this.state = {
        menuTop: [
          {
            link: '/',
            text: 'Give a start',
          },
          {
            link: '/faq',
            text: 'FAQ',
          }
        ],
        listFoo: ["CV Powered by: ", "Javascript", "React", "Components", "Css3 Animation", "Three.js", "TweenMax.js"],
        start: Myjson,
        faq: MyFaq,
      }
    }
    
    render() {
        return (
          <>
            <div className="row bg-light">
              <Header list={ this.state.start } />
            </div>
            <div className="row">
              <Snikos3d />
            </div>
            <div className="row topMenu">
              <Sidebar exp={ this.state.start } nav={ this.state.menuTop } />
              <Switch>
                <Route exact path="/" render={ () => <GiveAStart { ...this.state.start } /> } />
                <Route exact path="/faq" render={ () => <Faq {...this.state.faq} /> } />
                <Route path="*" component={PageNotFound} />
              </Switch>
            </div>
            <div className="row bg-light">
              <Footer lists={ this.state.listFoo } />
            </div>
          </>
        );
    }
}
