import React from "react";
import { Link } from 'react-router-dom';

export function SideMainMenu(props){
  const goods = Object.values(props.navigation).map(el => el);

  const [activeClass, setActiveClass] = React.useState(goods[0].text);
  const toggleActiveQuestion = (event) => {
    let target = event.target.textContent;
    setActiveClass(target);
  }
  
	return (
		<>
      <ul className="list-group">
      { goods.length && goods.map( ({ link, text }, index) => {
        return <li 
          key={ index } 
          className={`list-group-item ${text===activeClass ? 'active ' : ''} super-id`}
          onClick={(e) => toggleActiveQuestion(e)}
        >
          <Link key={text} to={link}>{text}</Link>
        </li>
      }) }
      </ul>
    </>
	)
}