import React from "react";

const Faq = (props) => {
	const faq = Object.values(props).map(el => el.typos);

    const [activeClass, setActiveClass] = React.useState(faq[0].list[0].question);
	const toggleActiveQuestion = (event) => {
		let target = event.target.textContent;
		setActiveClass(target);
	}

	return (
		<div className="col-12 col-sm-12 col-md-8 col-lg-9">
		    <h3>FAQ</h3>
		    <ul className="list-group">{
		    	faq.length > 0 && faq.map( ({name, list}, index) => (
		    	    <li key={index} className="list-group-item">
		    	        <h5>{name}<span className="badge badge-pill badge-success">{list.length}</span></h5>
		    	        {
		    	    	list.length && list.map(({question, answer}, idx) => (
		    	    	    <div key={idx} className={`coder-box ${question===activeClass ? 'food' : ''}`}>
		    	    	        <p 
		    	    	            className={`coder-title list-group-item ${question===activeClass ? 'active ' : ''}pre-line`}
		    	    	            onClick={(e) => toggleActiveQuestion(e)}>{question}</p>
		    	    	        <p className="coder pre-line">{answer}</p>
		    	    	    </div>
		    	    	))
		    	    }</li>
		    	))
		    }</ul>
		</div>
	)
}

export { Faq }